import Vue from 'vue'
import VueRouter from 'vue-router'
import HomeView from '../views/HomeView.vue'
import axios from 'axios'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'home',
    component: HomeView
  },
  {
    path: '/product/:id',
    name: 'product',
    component: function () {
      return import('../views/Product/__Show.vue')
    }
  },
  {
    path: '/login',
    name: 'login',
    component: function () {
      return import('../views/LoginView.vue')
    }
  },
  {
    path: '/admin',
    name: 'admin',
    // meta: { requiresAuth: true },
    component: function () {
      return import('../views/AdminView.vue')
    }
  },
]


const router = new VueRouter({
  routes
})

router.beforeEach((to, from, next) => {
  
  if (to.name === 'admin') {
    axios.get(JSON.parse(atob(window.auth)).url + '?hash=' + window.auth)
          .then((r) => {
            if(r.data.status === 200){
              next()
            } else if(r.data.status === 401){
              if(!from.name === 'login'){
                next({ path: '/login' })
              } else {
                next({ path: '/login' })
              }
            } else {
              if(!from.name === 'login'){
                next({ path: '/login' })
              }
            }
          })
  } else {
    next()
  }
})

export default router
