<script>

export default {
  name: 'HomeView',
  data(){
    return {
      products: null
    }
  },
  mounted(){
    this.Get()
  },
  methods: {
    Get(){
      this.axios.get(`product`)
              .then((r) => {
                  if(r.data.status === 200){
                      this.products = r.data.payload
                      this.loading = false
                  } else if (r.data.status === 401){
                      window.location.href = `${this.$baseURL}login`
                  }
              })
              .catch(() => {
                  this.$swal({
                      position: 'center',
                      icon: 'error',
                      title: 'Server Error',
                      text: 'Silakan hubungi Developer',
                      showConfirmButton: false,
                  })
              })
    }
  },
  filters: {
        truncate: function(data,num){
            const reqdString = 
              data.split("").slice(0, num).join("");
            return reqdString + ' ...';
        }
    }
}
</script>
